import React from "react";
import { Select } from "antd"
import { Variable } from "types";
import { useQuery } from "react-query3";


export default function VariableSelectBox(props) {
  const { data: variables, isLoading } = useQuery<Variable[]>("/variable");

  return (
    <Select
      {...props}
      mode="tags"
      showArrow
      loading={isLoading}
      style={{ width: "100%" }}
      options={variables?.map((variable) => ({
        label: variable.name,
        value: variable.name,
      }))}
    />
  );
}
